body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  padding-top: 4rem;
  padding-bottom: 4rem;
}

.margin {
  margin-top: 1.2rem;
}

.header {
  display: flex;
  max-width: 50%;
  width: 80%;
  gap: 15px;
}

.stealthAddressText {
  text-align: justify;
  text-justify: auto;
}

@media only screen and (max-width: 400px) and (max-height: 900px) {
  textarea {
    width: 50vmin !important;
    min-height: 8vmin;
    font-size: 1rem !important;
  }
}

@media only screen and (max-width: 500px) {
  textarea {
    width: 50vmin !important;
    height: 5rem !important;
    font-size: 1rem !important;
  }
  .header {
    flex-direction: column;
    width: 90%;
    gap: 25px;
  }
  .imageStealthAddress {
    transform: scale(1.8);
  }
  .headerStealth {
    font-size: 24px;
    text-align: center;
  }
  .result-container{
    transform: scale(60%);
  }
  #image-logo {
    margin-right: 0 !important;
    margin-top: 2rem;
  }
}

@media only screen and (max-width: 400px) {
  .imageStealthAddress {
    transform: scale(2);
  }
  .headerStealth {
    font-size: 19px;
  }
  .result-container{
    transform: scale(80%);
  }
}

.result {
  max-width: 70vmin;
  border: 1px solid black;
  overflow: hidden;
  inline-size: 60vmin;
  overflow-wrap: break-word;
  padding: 0.5rem;
  cursor: pointer;
  margin-right: 2rem;
}

.no-result {
  max-width: 90vmin;
  border: 1px solid black;
  overflow: hidden;
  inline-size: 80vmin;
  overflow-wrap: break-word;
  padding: 0.3rem;
}

.result-container {
  display: flex;
  align-items: center;
}

textarea {
  width: 80vmin;
  font-size: 1.2rem;
}

#image-logo {
  align-self: center;
  width: 15vmin;
  margin-right: 2rem;
}

button {
  width: 16rem;
  height: 3.5rem;
  font-size: 1rem;
  background: rgb(0,91,201);
  background: linear-gradient(90deg, rgba(0,91,201,0.8) 0%, rgba(0,176,103,0.8) 100%);
  color: white;
  cursor: pointer;
  border-radius: 1rem;
  box-shadow: none;
  border: none;
}

button:hover {
  background: rgb(0,91,201);
  background: linear-gradient(90deg, rgba(0,91,201,1) 0%, rgba(0,176,103,1) 100%);
}

label {
  font-weight: bold;
}
